<template>
  <v-container>
    <v-row>
      <v-col md="8" offset-md="2">
        <v-card class="pa-2" :disabled="submitting">
          <div v-if="loading">
            <v-card-text>
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
            </v-card-text>
          </div>
          <div v-else>
            <v-card-subtitle class="d-flex">
              <v-icon class="ml-2 mr-5">mdi-email-outline</v-icon>
              <div>
                <h3 class="font-weight-black">
                  Ticket #{{ id }} - {{ subject }}
                </h3>
                {{ createdAt | dateFormat }}
                (
                <i>{{ createdAt | setFromNow }}</i>
                )
              </div>
            </v-card-subtitle>

            <v-card-text class="d-flex">
              <v-img
                class="mr-3 rounded"
                :src="require('@/assets/default_selfie.jpg')"
                max-height="40"
                max-width="40"
              ></v-img>
              <div>
                <div class="font-weight-bold">{{ client }}</div>
                <div>{{ email }}</div>
                <div v-show="loanNumber && project && unit && contact">
                  <br />
                  <div>
                    <span class="font-weight-medium">Loan No.:</span>
                    {{ loanNumber }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Project:</span>
                    {{ project }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Unit/Lot No.:</span>
                    {{ unit }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Contact:</span>
                    {{ contact }}
                  </div>
                </div>
              </div>
            </v-card-text>

            <v-card-text class="d-flex">
              <v-icon small class="ml-3 mr-6">mdi-email-outline</v-icon>
              <div class="pre-formatted">
                {{ description }}
              </div>
            </v-card-text>

            <v-card-text>
              <div
                v-for="item in replies"
                :key="item.id"
                class="rounded py-3 mb-3"
                :style="(item.user_id) ? 'background-color: #e6e6e6' : 'border: 1px solid #e6e6e6'"
              >
                <div class="ml-13">
                  <div>
                    <span class="font-weight-bold">{{ item.agent }}</span>
                    replied
                  </div>
                  <i>{{ item.created_at | setFromNow }}</i>
                </div>
                <br />
                <div class="d-flex">
                  <v-icon small class="ml-3 mr-6">mdi-email-outline</v-icon>
                  <div class="pre-formatted">
                    {{ item.message }}
                  </div>
                </div>
                <div class="ml-13 mt-8" v-show="item.attachments">
                  <v-btn
                    class="text-capitalize"
                    small
                    :color="(item.user_id) ? 'grey lighten-1' : 'grey lighten-4'"
                    @click="download(item.id)"
                    :disabled="downloading"
                  >
                    <v-icon small>mdi-file-download-outline</v-icon>
                    {{ item.attachments ? item.attachments.substring(14) : "" }}
                  </v-btn>
                </div>
              </div>
            </v-card-text>

            <v-card-text v-show="showReply">
              <div
                class="px-3 py-2 rounded-t"
                style="background-color: #1976d2; color: #ffffff"
              >
                <v-icon small dark>mdi-reply-outline</v-icon> To: {{ agentEmail }}
              </div>
              <v-row>
                <v-col cols="12">
                  <v-textarea
                    ref="reply"
                    v-model="reply"
                    :rules="[
                      rules.required,
                      rules.minimum(2),
                      rules.maximum(5000),
                    ]"
                    outlined
                    dense
                    rows="8"
                    class="rounded-0"
                    hide-details="auto"
                  ></v-textarea>
                </v-col>
              </v-row>
              <div
                class="px-3 pt-3 pb-2 rounded-b d-flex"
                style="background-color: #1976d2; color: #ffffff"
              >
                <v-btn class="text-capitalize mr-2" small @click="sendReply">
                  <v-icon small>mdi-send-outline</v-icon>
                  Send
                </v-btn>

                <v-file-input
                  ref="attachment"
                  v-model="attachment"
                  label="Attachment"
                  dark
                  dense
                  hide-details="auto"
                  class="mt-0"
                ></v-file-input>

                <v-spacer></v-spacer>
                <v-btn icon small dark @click="showReply = false">
                  <v-icon small>mdi-trash-can-outline</v-icon>
                </v-btn>
              </div>
            </v-card-text>

            <v-card-actions v-show="showReply == false">
              <v-btn class="text-capitalize ml-3" small @click="writeReply">
                <v-icon small>mdi-reply-outline</v-icon>
                Reply
              </v-btn>
            </v-card-actions>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
import formRules from "@/helpers/formRules";

export default {
  data: () => ({
    rules: formRules,
    id: null,
    subject: null,
    createdAt: null,
    client: null,
    email: null,
    loanNumber: null,
    project: null,
    unit: null,
    contact: null,
    description: null,
    agent:null,
    agentEmail: null,
    showReply: false,
    reply: null,
    attachment: null,
    downloading: false,
  }),

  created() {
    this.getRecord();
  },

  computed: {
    ...mapGetters("reply", ["loading", "ticket", "replies", "submitting"]),
  },

  methods: {
    ...mapActions("reply", ["showRecord", "doReply", "doDownload"]),

    async getRecord() {
      await this.showRecord(this.$route.params.id);

      if (Object.keys(this.ticket).length) {
        this.id = this.ticket.id;
        this.subject = this.ticket.subject;
        this.createdAt = this.ticket.created_at;
        this.client = this.ticket.client;
        this.email = this.ticket.email;
        this.loanNumber = this.ticket.loan_number;
        this.project = this.ticket.project;
        this.unit = this.ticket.unit;
        this.contact = this.ticket.contact;
        this.description = this.ticket.description;
        this.agent = this.ticket.agent;
        this.agentEmail = this.ticket.agent_email;
      }
    },

    writeReply() {
      this.showReply = true;
      this.reply = `Hi ${this.agent},

`;
    },

    async sendReply() {
      if (this.reply) {
        if (!this.submitting) {
          const replyData = {
            ticket_id: this.id,
            message: this.reply,
            attachments: this.attachment,
          };

          await this.doReply(replyData);
          this.showReply = false;
          this.attachment = null;
        }
      } else {
        alert("Reply is required.");
      }
    },

    async download(id) {
      this.downloading = true;
      const downloadData = {
        id: id,
      };
      await this.doDownload(downloadData);
      this.downloading = false;
    },
  },

  filters: {
    dateFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },

    setFromNow(date) {
      return moment(date).fromNow();
    },
  },

  watch: {
    showReply(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.reply.focus();
        }, 200);
      }
    },
  },
};
</script>

<style scoped>
  .pre-formatted {
    white-space: pre;
  }
  </style>
