import axios from 'axios'

export default {
    namespaced: true,

    state: {
        loading: false,
        ticket: {},
        replies: [],
        submitting: false,
    },

    getters: {
        loading: (state) => (state.loading),
        ticket: (state) => (state.ticket),
        replies: (state) => (state.replies),
        submitting: (state) => (state.submitting),
    },

    mutations: {
        setLoading: (state, payload) => (state.loading = payload),
        setTicket: (state, payload) => (state.ticket = payload),
        setRelies: (state, payload) => (state.replies = payload),
        setSubmitting: (state, payload) => (state.submitting = payload),
        newReply: (state, payload) => state.replies.push(payload),
    },

    actions: {
        async showRecord({ commit }, data) {
            commit("setLoading", true)

            try {
                const response = await axios.get(`v1/tickets/reply/${data}`, {
                    headers: {
                        Authorization: 'Basic aGVscGxpbmUtc2FuZGJveDpobCEzMX5zYW5kYm94'
                    }
                });

                commit('setTicket', response.data.ticket);
                commit('setRelies', response.data.replies);
            } catch (error) {
                alert("The system encountered an error. Please refresh the page.")
            } finally {
                commit("setLoading", false)
            }
        },

        async doReply({ commit }, data) {
            commit("setSubmitting", true)

            try {
                const response = await axios.post('v1/tickets/reply', data, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        'Authorization': 'Basic aGVscGxpbmUtc2FuZGJveDpobCEzMX5zYW5kYm94'
                    }
                });

                commit('newReply', response.data.data);
                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            } finally {
                commit("setSubmitting", false)
            }
        },

        async doDownload({ commit }, data) {
            commit('setNotification', {
                type: 'warning',
                message: 'Under maintenance.'
            }, { root: true })
            // try {
            //     await axios.post('v1/tickets/my-queue/download', data);

            //     commit('setNotification', {
            //         type: 'success',
            //         message: 'Download successfully.'
            //     }, { root: true })
            // } catch (error) {
            //     if (error.response.status === 422) {
            //         commit("setErrors", error.response.data.errors)
            //     } else {
            //         alert('The system encountered an error. Please refresh the page.');
            //     }
            // }
        },
    }
}
