<template>
  <v-app id="app">
    <Navigation v-if="!isReply" />

    <v-main>
      <router-view />
    </v-main>

    <notification-list />
  </v-app>
</template>

<script>
import Navigation from "@/components/Navigation.vue";
import NotificationList from "./components/NotificationList.vue";

export default {
  name: "App",

  components: {
    Navigation,
    NotificationList,
  },

  computed: {
    isReply() {
      return this.$route.name === "Reply";
    },
  },
};
</script>

<style scoped>
#app {
  background-color: whitesmoke;
}
</style>
